"use client";
import { useEffect, useState } from 'react';
import { Menu, X } from 'lucide-react';

const links = [
  { href: '#home', label: 'Acceuil' },
  { href: '#about', label: 'A propos de moi' },
  { href: '#services', label: 'Mes services' },
  { href: '#work', label: 'Mon travail' },
  { href: '#contact', label: 'Me contacter' },
];

export default function Navbar() {
  const [open, setOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const onScroll = () => setScrolled(window.scrollY > 8);
    window.addEventListener('scroll', onScroll);
    return () => window.removeEventListener('scroll', onScroll);
  }, []);

  return (
    <header className={`fixed top-0 w-full z-50 transition-all ${scrolled ? 'bg-black/60 backdrop-blur border-b border-white/10' : ''}`}>
      <nav className="container-base flex items-center justify-between h-14">
        <a href="#home" className="font-semibold tracking-wide">Ben EphraÏm AGBANNON</a>
        <div className="hidden md:flex items-center gap-6">
          {links.map(l => (
            <a key={l.href} href={l.href} className="text-sm text-white/80 hover:text-white transition">
              {l.label}
            </a>
          ))}
        </div>
        <button className="md:hidden" aria-label="menu" onClick={() => setOpen(s => !s)}>
          {open ? <X className="h-6 w-6" /> : <Menu className="h-6 w-6" />}
        </button>
      </nav>
      {open && (
        <div className="md:hidden border-t border-white/10 bg-black/80">
          <div className="container-base py-4 flex flex-col gap-3">
            {links.map(l => (
              <a key={l.href} href={l.href} onClick={() => setOpen(false)} className="text-white/90">
                {l.label}
              </a>
            ))}
          </div>
        </div>
      )}
    </header>
  );
}
